<template>
    <div>
        <div class="card text-center border-dark mb-3">
            <div class="input-group card-header mb-3" v-if="edittingList[item.id]">
                <div class="input-group-prepend">
                    <span class="input-group-text" :id="`${item.id}_title`"></span>
                </div>
                <input type="text" class="form-control" :placeholder="item.title" aria-label=""
                    :aria-describedby="`${item.id}_title`" v-model="editItem[item.id].title" />
            </div>
            <h2 v-else class="card-header">{{ item.title }}</h2>
            <div class="card-body">
                <div class="input-group mb-3" v-if="edittingList[item.id]">
                    <div class="input-group-prepend">
                        <span class="input-group-text" :id="`${item.id}_imageUrl`"></span>
                    </div>
                    <input type="text" class="form-control" :placeholder="item.imageUrl" aria-label=""
                        :aria-describedby="`${item.id}_imageUrl`" v-model="editItem[item.id].imageUrl" />
                </div>
                <img v-if="edittingList[item.id]" :src="editItem[item.id].imageUrl" alt="" />
                <img v-else :src="item.imageUrl" alt="" class="coin card-img-top" />

                <div class="input-group mb-3" v-if="edittingList[item.id]">
                    <div class="input-group-prepend">
                        <span class="input-group-text" :id="`${item.id}_content`"></span>
                    </div>
                    <input type="text" class="form-control" :placeholder="item.content" aria-label=""
                        :aria-describedby="`${item.id}_content`" v-model="editItem[item.id].content" />
                </div>
                <div v-else>{{ item.content }}</div>
                <div class="row">
                    <div class="input-group mb-3 col" v-if="edittingList[item.id]">
                        <div class="input-group-prepend">
                            <span class="input-group-text" :id="`${item.id}_origin_price`"></span>
                        </div>
                        <input type="number" class="form-control" :placeholder="item.origin_price" aria-label="" min="0"
                            :aria-describedby="`${item.id}_origin_price`" v-model.number="editItem[item.id].origin_price" />
                    </div>
                    <del v-else class="col card-text">:{{ item.origin_price }}</del>
                    <div class="input-group mb-3 col" v-if="edittingList[item.id]">
                        <div class="input-group-prepend">
                            <span class="input-group-text" :id="`${item.id}_price`"></span>
                        </div>
                        <input type="number" class="form-control" :placeholder="item.price" aria-label=""
                            :aria-describedby="`${item.id}_price`" v-model.number="editItem[item.id].price" />
                    </div>
                    <div v-else class="col card-text">:{{ item.price }}</div>
                </div>
                <div>
                    <button v-if="!edittingList[item.id]" key="" class="btn btn-outline-success"
                        @click="editProduct(item)"></button>
                    <button v-if="edittingList[item.id]" key="" class="btn btn-outline-success"
                        @click="editProductCancel(item.id)"></button>
                    <button v-if="edittingList[item.id]" key="" class="btn btn-outline-success"
                        @click="editProductDone(item.id)"></button>
                    <button class="btn btn-outline-danger" data-bs-toggle="modal" data-bs-target="#deletModal"
                        @click="openProduct(item)"></button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import axios from 'axios';

export default {
    name: "ProductCoin",
    props: ["item"],
    data() {
        return {
            edittingList: {},
            editItem: {},
            apiUrl: "https://vue3-course-api.hexschool.io/v2",
            apiPath: "wuyi0020",
        }
    },
    methods: {
        editProduct(item) {
            this.edittingList[item.id] = !this.edittingList[item.id];
            this.editItem[item.id] = { ...item };
        },
        editProductCancel(id) {
            this.edittingList[id] = !this.edittingList[id];
            this.editItem[id] = "";
        },
        editProductDone(id) {
            if (this.editItem[id].title === "") {
                alert("");
                return;
            }
            if (this.editItem[id].origin_price === "") {
                alert("");
                return;
            }
            if (this.editItem[id].price === "") {
                alert("");
                return;
            }
            const url = `${this.apiUrl}/api/${this.apiPath}/admin/product/${id}`;
            let putItem;
            this.edittingList[id] = !this.edittingList[id];

            putItem = { ...this.editItem[id] };
            delete putItem.id;
            axios.put(url, { data: putItem })
                .then(() => {
                    this.$emit("getcoinData");
                    alert("");
                })
                .catch((err) => {
                    alert(err.response);
                })
        },
        openProduct(item) {
            this.$emit("openProduct", item);
        }
    },
};
</script>
